@import url(~antd/dist/antd.css);
.App {
  text-align: center; }

#root {
  width: 100%;
  height: 100%; }

.ToDo {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 100px;
  padding-bottom: 100px; }
  .ToDo .add-section {
    display: flex; }
  .ToDo .todo-list {
    text-align: center; }
    .ToDo .todo-list .anticon-loading {
      font-size: 30px; }
  .ToDo .ToDoItem {
    display: flex;
    margin-top: 10px;
    margin-bottom: 10px; }
  .ToDo .profile-link {
    text-align: center;
    margin-bottom: 20px;
    font-size: 20px; }

.flex {
  display: flex; }

.ant-input-disabled {
  background-color: #e0e0e040; }

body {
  font-weight: 700px;
  background-color: #798998; }
